.card {
  display: block;
}
.card > p {
  font-size: 14pt;
  color: #effffe;
  color: var(--text-color);
  text-align: center;
}
.card > p > a > em {
  text-decoration: none;
  position: relative;
  color: #e5e5a8;
  color: var(--secondary);
  cursor: pointer;
  font-style: normal;
}
.card > p > a > em::before {
  content: "";
  position: absolute;
  bottom: -2px;
  right: 0;
  width: 0;
  height: 4px;
  border-radius: 5px;
  background-color: #7879f1;
  background-color: var(--primary);
  transition: width 1s cubic-bezier(0.25, 1, 0.5, 1);
}
.card > p > a > em:hover::before {
  left: 0;
  right: auto;
  width: 100%;
}

@media (min-width: 800px) {
  #root,
  .App,
  .container {
    height: 100%;
