.navbar {
  position: fixed;
  display: flex;
  bottom: 0;
  height: 80px;
  width: 100%;
  z-index: 999;
  min-width: 385px;
    justify-content: center;
  /* background-color: var(--secondary); */
}

.movable-navbar {
  display: flex;
  position: fixed;
  width: 100%;
  min-width: 385px;
  height: 80px;
  background-color: var(--secondary);
  z-index: -1;
  justify-content: center;
  transition: all cubic-bezier(0.215, 0.61, 0.355, 1) 0.5s;
}
.movable-navbar .prev-item {
  position: absolute;
  height: 80px;
  width: 80px;
  margin-right: 485px;
  background-color: var(--terciary);
}
.movable-navbar .next-item {
  position: absolute;
  height: 80px;
  width: 80px;
  margin-right: 165px;
  background-color: var(--terciary);
}
.movable-navbar .current-item {
  position: relative;
  height: 80px;
  width: 80px;
  margin-right: calc(325px);
  border-radius: 0 0 50% 50%;
  background-color: var(--terciary);
}
.movable-navbar .current-item::before {
  content: "";
  position: absolute;
  height: 80px;
  width: 80px;
  right: 80px;
  border-radius: 0 50% 0 0;
  background-color: var(--secondary);
}
.movable-navbar .current-item::after {
  content: "";
  position: absolute;
  height: 80px;
  width: 80px;
  left: 80px;
  border-radius: 50% 0 0 0;
  background-color: var(--secondary);
}

.navbar-items {
  display: flex;
  color: var(--primary);
  height: 100%;
  max-width: 400px;
  list-style-type: none;
  margin: auto;
  justify-content: space-around;
  align-items: center;
}

.navbar-item {
  display: flex;
  height: 100%;
  width: 80px;
  align-items: center;
  font-size: clamp(24pt, 4vw, 2.75rem);
  justify-content: center;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  cursor: pointer;
  /* background-color: var(--secondary);  */
  -webkit-tap-highlight-color: transparent;
  transition: all ease 0.2s;
}

.navbar-item > * {
  pointer-events: none;
  z-index: 4;
}

.navbar-item.active {
  position: relative;
  height: 100%;
  width: 80px;
  color: var(--secondary);
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
  font-size: clamp(28pt, 4vw, 3rem);
  transition: all cubic-bezier(0.165, 0.84, 0.44, 1) 0.5s;
}
.navbar-item-circle {
  z-index: 2;
  background-color: var(--primary);
  width: 100px;
  height: 100px;
  color: var(--secondary);
  border-radius: 50%;
}

